  border: 2px solid;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: transform .3s;
  display: flex;
  position: absolute;
  top: 2%;
  transform: translate(0);
}

.card:hover .movie-poster, .card:focus .movie-poster {
  cursor: pointer;
  transform: scale(1.02);
  box-shadow: 0 0 10px 1px gray;
}

@media screen and (width >= 320px) {
  .gallery {
    width: 320px;
  }

  .no-results-info {
    width: 300px;
    height: 145px;
  }

  .rating-info {
    width: 33px;
    height: 33px;
    left: 85%;
  }

  .card:hover .rating-info {
    transform: translate(2px, -4px);
  }
}

@media screen and (width >= 768px) {
  .gallery {
    gap: 32px;
    width: 768px;
    padding-top: 60px;
  }

  .card {
    width: 336px;
    height: 494px;
  }

  .movie-poster {
    height: 455px;
  }

  .no-results-info {
    width: 400px;
    height: 208px;
  }

  .rating-info {
    width: 35px;
    height: 35px;
  }

  .card:hover .rating-info {
    transform: translate(3px, -4px);
  }
}

@media screen and (width >= 1280px) {
  .gallery {
    justify-content: flex-start;
    gap: 16px;
    width: 1280px;
  }

  .card {
    width: 395px;
    height: 634px;
    margin-bottom: 16px;
  }

  .movie-poster {
    height: 574px;
  }

  .movie-title, .movie-subtitle {
    font-size: 20px;
    line-height: 1.2;
  }

  .rating-info {
    width: 40px;
    height: 40px;
    left: 86%;
  }

  .card:hover .rating-info {
    transform: translate(3px, -5px);
  }
}

