.loader__div {
  color: #ccc;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 100px;
  height: 100px;
  padding: 10px;
  transition: all .2s linear;
  display: flex;
  position: relative;
}

.loader {
  text-align: center;
  border: 5px solid #ff6b08;
  width: 48px;
  height: 48px;
  animation: 3s linear infinite rotate;
  position: absolute;
  transform: perspective(200px)rotateX(50deg);
}

.loader:before {
  content: "";
  border: 5px solid #f7f7f7;
  width: 100%;
  height: 100%;
  animation: 1.5s linear infinite reverse rotate;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

